<template>
    <div class="login-container">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="login-form" label-width="0px" v-show="!isMobileShow">
            <div class="title-container">
                <h3 class="title">{{systemprofilesTitle}}</h3>
            </div>

            <el-form-item prop="username">
        <span class="svg-container">
          <svg-icon icon-class="user"/>
        </span>
                <el-input
                        ref="username"
                        v-model="ruleForm.username"
                        placeholder="请输入账号"
                        type="text"
                        tabindex="1"
                        auto-complete="on"
                        @keyup.enter.native="submitForm('ruleForm')"
                />
            </el-form-item>

            <el-form-item prop="password">
        <span class="svg-container">
          <svg-icon icon-class="password"/>
        </span>
            <el-input
                    :key="passwordType"
                    ref="password"
                    v-model="ruleForm.password1"
                    :type="passwordType"
                    placeholder="请输入密码"
                    tabindex="2"
                    auto-complete="on"
                    @keyup.enter.native="submitForm('ruleForm')"
            />
            <span class="show-pwd" @click="showPwd">
          <svg-icon :icon-class="passwordType === 'password' ? 'eye' : 'eye-open'"/>
        </span>
        </el-form-item>

            <el-form-item prop="domainName" v-show="showDomainName">
        <span class="svg-container">
          <svg-icon icon-class="link"/>
        </span>
                <el-input
                        ref="domainName"
                        v-model="ruleForm.domainName"
                        type="text"
                        placeholder="请输入企业号"
                        tabindex="3"
                        auto-complete="on"
                        @keyup.enter.native="submitForm('ruleForm')"
                />
            </el-form-item>

            <el-button :loading="loading" type="primary" style="width:100%;margin-bottom:30px;"
                       @click="submitForm('ruleForm')">
                登录
            </el-button>

        </el-form>
        <div v-show="isMobileShow" class="mobile-tip">
            <h1>本演示地址是管理后台的功能，请使用PC端打开访问 http://www.live58.cn</h1>
        </div>
    </div>
</template>

<style lang="scss">
    /* 修复input 背景不协调 和光标变色 */
    /* Detail see https://github.com/PanJiaChen/vue-element-admin/pull/927 */

    $bg:#283443;
    $light_gray:#fff;
    $cursor: #fff;

    @supports (-webkit-mask: none) and (not (cater-color: $cursor)) {
        .login-container .el-input input {
            color: $cursor;
        }
    }

    /* reset element-ui css */
    .login-container {
    .el-input {
        display: inline-block;
        height: 47px;
        width: 85%;

    input {
        background: transparent;
        border: 0px;
        -webkit-appearance: none;
        border-radius: 0px;
        padding: 12px 5px 12px 15px;
        color: $light_gray;
        height: 47px;
        caret-color: $cursor;

    &:-webkit-autofill {
         box-shadow: 0 0 0px 1000px $bg inset !important;
         -webkit-text-fill-color: $cursor !important;
     }
    }
    }

    .el-form-item {
        border: 1px solid rgba(255, 255, 255, 0.1);
        background: rgba(0, 0, 0, 0.1);
        border-radius: 5px;
        color: #454545;
    }
    }
</style>

<style lang="scss" scoped>
    $bg:#2d3a4b;
    $dark_gray:#889aa4;
    $light_gray:#eee;

    .login-container {
        min-height: 100%;
        width: 100%;
        background-color: $bg;
        overflow: hidden;

        position: absolute;
        top: -15px;
        bottom: 0;
        left: 0;
        right: 0;
        margin: 0px;

    .login-form {
        position: relative;
        width: 520px;
        max-width: 100%;
        padding: 160px 35px 0;
        margin: 0 auto;
        overflow: hidden;
    }

    .tips {
        font-size: 14px;
        color: #fff;
        margin-bottom: 10px;

    span {
    &:first-of-type {
         margin-right: 16px;
     }
    }
    }

    .svg-container {
        padding: 6px 5px 6px 15px;
        color: $dark_gray;
        vertical-align: middle;
        width: 30px;
        display: inline-block;
    }

    .title-container {
        position: relative;

    .title {
        font-size: 26px;
        color: $light_gray;
        margin: 0px auto 40px auto;
        text-align: center;
        font-weight: bold;
    }
    }

    .show-pwd {
        position: absolute;
        right: 10px;
        top: 7px;
        font-size: 16px;
        color: $dark_gray;
        cursor: pointer;
        user-select: none;
    }
    }

    .mobile-tip{
        padding: 20px;
        margin-top: 50px;
        color: red;
    }
</style>

<script>
    import md5 from 'js-md5';

    export default {
        data() {
            return {
                isMobileShow:false,
                showDomainName:false,
                ruleForm: {
                    username: 'admin',
                    password: '',
                    password1: '123',
                    domainName: 'cs.com',
                }, rules: {
                    username: [
                        {required: true, message: '请输入账号', trigger: 'blur'},
                    ],
                    password1: [
                        {required: true, message: '请输入密码', trigger: 'blur'},
                    ],
                },
                loading: false,
                passwordType: 'password',
                systemprofilesTitle: '',
            }
        },
        mounted() {
            if (this.isMobile()) {
                this.isMobileShow = true;
            }
            this.axios.get('/open/system/init').then((response) => {
                    if (response.data.state == '0') {
                        let data = response.data.data;
                        // console.log(data)
                        this.showDomainName = data.is_public_cloud==='false'?false:true;
                        if(!this.showDomainName){
                            this.ruleForm.domainName = data.private_cloud_domain;
                        }
                    }
                }
            ).catch((response) => {
                console.log(response);
            })
            if(this.$cookies.isKey("systemprofiles_title")){
                document.title = this.$cookies.get("systemprofiles_title");
                this.systemprofilesTitle = this.$cookies.get("systemprofiles_title");
            }
            this.axios.get('/open/user/get').then((response) => {
                if (response.data.data != null && response.data.data.userId != null) {
                    this.$router.push({path: '/', replace: true});
                }
            }).catch((response) => {
                console.log(response);
            })
        },
        methods: {
            isMobile() {
                let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
                return flag;
            },
            showPwd() {
                if (this.passwordType === 'password') {
                    this.passwordType = ''
                } else {
                    this.passwordType = 'password'
                }
                this.$nextTick(() => {
                    this.$refs.password.focus()
                })
            },
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.ruleForm.password = md5(this.ruleForm.password1);
                        this.loading = true;
                        this.axios.post('/api/login', null, {
                            params: {
                                username: this.ruleForm.username,
                                password: this.ruleForm.password,
                                domainName: this.ruleForm.domainName
                            }
                        }).then((response) => {
                            console.log(response);
                            this.ruleForm.password1 = '';
                            this.ruleForm.password = '';
                            if (response.data.state == '0') {
                                //登录成功
                                this.$router.push({path: '/', query: {source: 'login'}, replace: true});
                            } else {
                                this.loading = false;
                                //登录失败
                                this.$message({
                                    type: 'error',
                                    message: '用户名或密码错误'
                                });
                            }
                        }).catch((response) => {
                            this.ruleForm.password = '';
                            console.log(response);
                            this.loading = false;
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            }
        }
    };
</script>
